import React, { useEffect, useState } from "react";
import d_max from "@styles/assets/images/hirescreens/media-icons/3d_max.svg";
import blender from "@styles/assets/images/hirescreens/media-icons/blender.svg";
import maya from "@styles/assets/images/hirescreens/media-icons/maya.svg";
import v_ray from "@styles/assets/images/hirescreens/media-icons/v_ray.svg";
import modo from "@styles/assets/images/hirescreens/media-icons/modo.svg";
import octanerender from "@styles/assets/images/hirescreens/media-icons/octanerender.svg";
import zbrush from "@styles/assets/images/hirescreens/media-icons/zbrush.svg";
import cinema4d from "@styles/assets/images/hirescreens/media-icons/cinema4d.svg";
import sketchup_light from "@styles/assets/images/hirescreens/media-icons/sketchup_light.svg";

import Photoshop from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_photoshop.svg";

import Ticker from "@components/common/Ticker";

const datas = [
  { id: 0, title: "3ds Max", icon: d_max },
  { id: 1, title: "Photoshop", icon: Photoshop },
  { id: 2, title: "Blender", icon: blender },
  { id: 3, title: "SketchUp", icon: sketchup_light },
  { id: 4, title: "Cinema 4D", icon: cinema4d },
  { id: 5, title: "ZBrush", icon: zbrush },
  { id: 6, title: "Octane Render", icon: octanerender },
  { id: 7, title: "Modo", icon: modo },
  { id: 8, title: "V-Ray", icon: v_ray },
  { id: 8, title: "Autodesk Maya", icon: maya },
];
const TechStackMarquee = () => {
  const [scrollDir, setScrollDir] = useState(null);

  useEffect(() => {
    let scrollSide = window.scrollY;

    const handleScroll = () => {
      let currentScrollSide = window.scrollY;

      if (currentScrollSide > scrollSide) {
        setScrollDir("down");
      } else if (currentScrollSide < scrollSide) {
        setScrollDir("up");
      }
      scrollSide = currentScrollSide;
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="hire-marquee-wrapper">
      <div className="hire-developers-hero-wrapper item-center gap-lg">
        <div className="item-center gap-lg">
          <h2 className="about-h2">Our Technology Stack</h2>
          <p className="about-p c-text-center">
            Our 3D Designers utilize advanced modeling and rendering software to
            create immersive visuals, animations, and product mockups that
            deliver depth, realism, and precision.
          </p>
        </div>
        <section className="token-dev-technology-marquee-section">
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? 100 : -100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? -100 : 100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
        </section>
      </div>
    </section>
  );
};

export default TechStackMarquee;
